.none{
    opacity: 0;
    cursor: default;
}
.wrapper{
   margin: 15px 0;
    svg{
        max-width: 20px;
        fill: #FE5F1E;
        
    }
    ul,li{
        display: flex;
        flex-direction: row;
        align-items: center;
    }
    li{

        width: 35px;
        height: 35px;
        border: 1px solid #FE5F1E;
        border-radius: 50%;
        margin: 0 5px;
        justify-content: center;
        cursor: pointer;
        transition: all .3s linear ;
        &:hover{
            background-color: #FE5F1E;
            color: #fff;
            svg{
                fill: #fff;
            }
        }
    }
    p{
        margin: 0;
    }
}
.active{
    color: #fff;
    background-color: #FE5F1E;
}
