.search{
    padding: 15px;
    border: 1px solid #bebebe;
    border-radius: 20px;
   display: inline-flex;
    align-items: stretch;
    input{
        outline: 0;
        border: 0;
        font-size: 1rem;
        margin: 0 5px;
        width: 100%;
    }
    > div{
        display: flex;
        align-items: center;
    }
}
.clear{
    button{
        padding: 10px;
        border: 1px solid #bebebe;
        border-radius: 50%;
        line-height: 1;
        background-color: #fff;
        cursor: pointer;
        font-weight: 600;
        position: relative;
        &::after,&::before{
            content: '';
            position: absolute;
            width: 60%;
            height: 1px;
            background-color: #bebebe;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%) rotate(45deg);
        }
        &::before{
            transform: translate(-50%,-50%) rotate(-45deg);
        }
    }
    
}


@media only screen and (max-width:768px) {
    .search{
        padding: 20px 15px;
    }
}