header, .left, .right, .cart__count{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  
    h3,p{
        margin:  0;
    }
   
}
header{
    flex-wrap: wrap;
    >a,>div{
        margin: 10px 0;
    }
}

.logo__name{
    margin: 0 0 0 15px;
    h3{
        font-size: 1.5rem;
    }
  
}
.logo{
    img{
        max-width: 100px;
    }
}

.right{
    background-color: #FE5F1E;
    border-radius: 30px;
    padding: 20px 15px;
    color: #fff;
    height: 100%;
    align-items: stretch;
    
    cursor: pointer;
}

.vertical__line{
    border: 1px solid #FFFFFF40;
    flex: 1;
    margin: 0 5px;

}
.cart__count{
    align-items: center;
    img{
        margin-top: -5px;
    }
}


@media only screen and (max-width:768px) {
    header{
        flex-direction: column;
        align-items: center;
    }
}