.sort{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    font-size: .9rem;
    flex-wrap: wrap;
    p{
        margin: 0 ;
    }
}
.sort__display{
    p:last-of-type{
        color: #FE5F1E;
        border-bottom: 1px dotted #FE5F1E ;
        cursor: pointer;
        margin: 0 0 0 5px;
    }
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.sort__items{
    position: absolute;
    bottom: -270px;
    right: 0;
    z-index: 100;
    background-color: #fff;
    border-radius: 0 0 20px 20px;
    
    ul{
        display: flex;
        flex-direction: column;
        padding: 15px 10px;
        li{
            padding: 11px;
            cursor: pointer;
            transition: all .3s ease-in-out;
            &:hover{
                color: #FE5F1E;
            }
        }
        .active{
            color: #FE5F1E;
        }
    }
   
}
.close{
    display: none;
}


@media only screen and (max-width:870px) {
    .sort{
        flex-wrap: wrap;
        gap:10px;
        justify-content: center;
        border: 1px solid #bebebe;
        padding: 15px 10px;
        border-radius: 20px;
        box-shadow: 0 0 10px #bebebe;  
        margin: 10px 0;

    }
}