.container{
    max-width: 900px;
    margin: 10px auto;
    background-color: #fff;
    padding:30px 40px;
    border-radius: 20px;
    opacity: 1;
    transition: all .5s ease-in-out;
    transform: translateX(100vw);
    overflow: hidden;
}

.content__show{
    transform: translateX(0);
}


.pizza{
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content:center;
    height: 100%;
    flex-wrap: wrap;
    
}

.descriptions{
    flex-basis: 40%;
    overflow: auto;
    height: 100%;
   
}

.pizza__img{
    flex-basis: 50%;
    text-align: center;
}
.close{
    position: absolute;
    top: -25px;
    right: -35px;
    border: 0;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    cursor: pointer;
    text-align: center;
    background: url('./../../../public/img/close.svg') center/cover;
    transition: all .5s ease-in-out;
    &:hover{
        transform: rotate(180deg);
    }
}

@media only screen and (max-width:590px) {
    .descriptions{
        flex-basis: 100%;
    }
    .pizza{

        img{
            width: 100%;
        }
    }
}