.item{
    margin: 10px 0;
}
.item, .count, .title{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    h4,p{
        margin: 0;
    }
    p{
        font-size: .8rem;
    }
    button{
        border: 1px solid #FE5F1E;
        border-radius: 50%;
        padding: 5px 10px;
        cursor: pointer;
        margin:  0 10px;
        background: none;
        transition: all .3s linear;
        &:disabled{
            opacity: .7;
            cursor: default;
        }
    }
    .delete{
        border-color: #b3b3b3;
        color: #b3b3b3;
    }
    img{
        max-width: 70px;
    }
}
.title{
    flex-basis: 30%;
    justify-content: flex-start;
    div{
        margin:  0 0 0 5px;
    }
}

.price{
    font-weight: 700;
}


@media only screen and (max-width: 530px) {
    .title{
        flex-direction: column;
        text-align: center;
    }
}

@media only screen and (max-width: 480px) {
    .item{
        position: relative;
        flex-direction: column;
        text-align: center;
        margin: 20px 0;
        border: 1px solid #bebebe;
        border-radius: 30px;
        padding: 15px 10px;
        box-shadow: 0 0 10px #ccc;
    }
    .delete{
        position: absolute;
        top: 10px;
        right: 10px;
    }
  
}