.app {
  background: rgb(214,56,39);
  background: linear-gradient(90deg, rgba(214,56,39,1) 0%, rgba(192,57,43,1) 36%, rgba(254,95,30,1) 100%);  min-height: 100vh;
  padding: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  border-radius: 10px;
  flex: 1;
  width: 100%;
  padding: 50px 30px;
}

@media only screen and (max-width:500px) {
  .app{
    padding: 15px;
  }
}