.item,
.pizza__options ul,
.price__cart,
.add__cart {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

    ul,
    .price__cart,
    .add__cart {
        flex-direction: row;
    }

}

.item {   
    flex-basis: calc(24% - 10px);
}
.pizza{
    transition: all .5s ease-in-out;
}
.img__container{
    position: relative;
    overflow: hidden;
    padding: 20px 0 0 0 ;
    height: 100%;
    width: 100%;
    &::after{
        content: '';
        display: block;
        padding-top: 100%;
    }
    &:hover{
        .slice{
            top: -16px;
            left: 8px;
            transform: scale(1.1);
            animation-name: slice;
            animation-duration: 1.5s;
            animation-timing-function: ease-in-out;
            animation-iteration-count: infinite;
            animation-delay: .5s;
        }
        .pizza{
            opacity: .7;

        }
        .description{
            bottom: 50%;
            left: 50%;
        }
    }
}
.img__wrapper{
    position: absolute;
    top: 20px;
    left: 0;
    height: 100%;
    width: 100%;
    cursor: pointer;
}
.img{
    position: relative;
    margin: auto;
    width: 100%;
    transform: rotate(360deg) scale(.8);
    transition: all .5s ease-in-out;
    img{
        width: 100%;
    }
}

.large{
    transform: rotate(720deg) scale(1);
}
.small{
    transform: rotate(0) scale(.6);
}
.slice{
    position: absolute;
    transition: all .5s ease-in-out;
    top: 0;
    left: 0;
    z-index: 1;
}
.description{
    position: absolute;
    bottom: 50%;
    left: -100%;
    padding: 10px 15px;
    background-color: #282828b2;
    color: #fff;
    border-radius: 20px;
    transform: translate(-50%, 100%);
    font-size: .7rem;
    transition: all 1s ease-in-out;
    width: 90%;
}

.pizza__options {
    padding: 7px 10px;
    background-color: #e4e2e2;
    border-radius: 10px;
    margin: 10px 0;

    
    
    li {
        padding: 5px 10px;
        margin: 5px 0;
        border-radius: 5px;
        font-size: .8rem;
        cursor: pointer;
        color: #282828;
        font-weight: 600;
        transition: all .5s ease-in-out;
    }
  

    .active {
        background-color: #fff;
    }
    .size__none{
        opacity: .5;
        cursor: default;
        background-color: transparent ;
    }
}

.price__cart {
    width: 100%;
 
    p{
        font-size: .9rem;
        white-space: nowrap;
        font-weight: 600;
    }
}

.add__cart {
    padding: 10px 15px;
    flex-direction: row;
    border: 1px solid #EB5A1E;
    background-color: #fff;
    border-radius: 30px;
    cursor: pointer;
    transition: all .3s ease-in-out;
    &:hover {
        background-color: #EB5A1E;
        .count{
            opacity: 0;
            width: 0;
            padding: 0;
        }
        p {
            color: #fff;
        }
    }

    .count {
        display: block;
        width: 20px ;
        height: 100%;
        color: #fff;
        background-color: #EB5A1E;
        border-radius: 50%;
        overflow: hidden;
        transition: padding .3s ease-in-out;
    }

    .text {
        margin: 0 5px;
        
    }
    

    p {
        margin: 0;
        font-weight: 700;
        color: #EB5A1E;

        &:first-of-type {
            font-size: 1.1rem;

        }
    }

}


.smoke{
    opacity: 0;
    width: 80%;
    height: 100%;
    left: 50%;
    bottom: -120px;
    margin: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    position: absolute;
    z-index: 10;
    transform: translateX(-50%);
    transition: all .4s ease-in-out;
    span{
        animation: smoke 3s linear infinite;
        display: block;
        flex-basis: 5px;
        margin: 0 1px;
        background-color: #f0f0f0;
        height: 200px;
        filter: blur(5px);
        border-radius: 50%;
    }
}


.show__smoke{
    opacity: 0.7;
}

@keyframes smoke {
    0% {
        transform: translateY(0) scaleX(1);
        opacity: 0;
      }
      15% {
        opacity: 1;
      }
      50% {
        transform: translateY(-150px) scaleX(5);
      }
      95% {
        opacity: 0;
      }
      100% {
        transform: translateY(-300px) scaleX(10);
        opacity: 0;
      }
}


@keyframes slice {
    0%{
        top: -16px;
        left: 8px;
    }
    50%{
        top: -10px;
        left: 4px;
    }
    100%{
        top: -16px;
        left: 8px;
    }
}

@media only screen and (max-width: 1160px) {
    .item{
        flex-basis: 30%;
    }
}


@media only screen and (max-width: 630px) {
    .item{
        flex-basis: 60%;
    }
    .price__cart{
        flex-wrap: wrap;
        justify-content: center;
    }
}