.home{
    padding:  60px 0 0 0;
    position: relative;
}
.sort__categories{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    > div:first-child{
        flex-basis: 70%;
    }
    > div:last-child{
        flex-basis: 25%;
    }
}
.items{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    gap: 20px;
}

.pizza__wrapper{
    display: flex;
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100vh;
    backdrop-filter: blur(10px);
    z-index: 1000;
    transition: all .5s ease-in-out;
    padding: 10px;
    overflow: hidden;
}

.pizza__show{
    left: 0;
}


@media only screen and (max-width:870px) {
    .sort__categories{
        align-items: stretch;
    }
}
@media only screen and (max-width:768px) {
    .home{
        padding: 0 ;
    }
    .sort__categories{
        flex-direction: column;
    }
}

@media only screen and (max-width: 620px) {
    .items{
        justify-content: center;
    }
}