.empty{
    display: flex;
    flex-direction: column;
    flex: 1;
    justify-content: center;
    text-align: center;
    width: 70%;
    margin: 0 auto;
    padding: 60px 0 0 0;
    img{
        display: block;
        max-width: 200px;
        margin: auto;
        width: 100%;
    }
    h3{
        margin: 0;
    }
}

.wrapper{
    display: flex;
    flex-direction: column;
    width: 60%;
    margin: 0 auto;
    padding: 30px 0;
    flex: 1;
    .title,.clear,.header{
        display: flex;
        flex-direction: row;
        align-items: center;
        h3{
            margin: 0 5px;
        }
    
    }
    .clear{
        cursor: pointer;
        color: #B6B6B6;
        font-size: .9rem;
    }
    .header{
        justify-content: space-between;
    }
    .items{
        flex: 1;
    }
}
.total__summ{
    color: #FE5F1E;
}
.total{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    p{
        margin: 10px 0;
    }
}

@media only screen and (max-width: 720px) {
    .wrapper{
        width: 100%;
    }
}

@media only screen and (max-width: 490px) {
    .wrapper{
        .total, .header{
            flex-direction: column;
        }
        .header{
            align-items: end;
        }
    }
    .empty{
        width: 100%;
    }
}


