.categories {
    margin: 10px 0;
    ul {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }

    li {
        color: #282828;
        font-weight: 700;
        border-radius: 30px;
        padding: 15px 25px;
        font-size: 1rem;
        cursor: pointer;
        transition: all .3s ease-in-out;

        &:hover {
            background: #d63827;
            color: #fff;
        }
    }

    .active {
        background: #d63827;
        color: #fff;
    }
}

@media only screen and (max-width:870px) {
    .categories{
        ul{
            flex-wrap: wrap;
            justify-content: start;
            border: 1px solid #bebebe;
            padding: 15px 10px;
            border-radius: 20px;
            box-shadow: 0 0 10px #bebebe;
        }
       
    }
}

@media only screen and (max-width:768px) {
    .categories{
        ul{
            justify-content: center;
        }
       
    }
}