.option{
    width: 90%;
    background-color: #c2bfbf;
    li{
        color: transparent;
    }
}
.img__container{
    position: relative;
    overflow: hidden;
    padding: 20px 0 0 0 ;
   
    > div{
        background-color: #c2bfbf;
        border-radius: 50%;
    }
   img{
    opacity: 0;
   }
}
.item{
    animation-name: loader;
    animation-duration: .7s;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    
    h4{
        display: block;
        width: 100%;
        color: transparent;
        background-color: #c2bfbf;
        border-radius: 20px;
    }
}
.add__cart {
    display: flex;
    padding: 10px 15px;
    border: 1px solid #EB5A1E;
    background-color: #c2bfbf;
    border-color: #c2bfbf;
    color: transparent;
    border-radius: 30px;
    cursor: pointer;
    transition: all .3s ease-in-out;
   

    .count {
        display: block;
        padding: 4px 8px;
       
    }

    .text {
        margin: 0 5px;
    }

    p {
        margin: 0;
        font-weight: 700;
       

        &:first-of-type {
            font-size: 1.1rem;

        }
    }   
}
.price{
    background-color: #c2bfbf;
    margin: 0;
    height: 100%;
    border-radius: 30px;
    color: transparent;
}

@keyframes loader {
    0%{
        opacity: 0.6;
    }

   

    100%{
        opacity: 1;
    }
}